@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
* {
  font-family: 'Press Start 2P', cursive;
  margin: 0;
}

input, textarea, button, select, a { -webkit-tap-highlight-color: rgba(0,0,0,0); }

body {
  background-color: #256DB5;
  width: 100%;
  min-height: 100vh;
}

.App {
  text-align: center;
  width: 100%;
  min-height: 100vh;
}
#number-players-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#number-players-box {
    width: 314px;
    height: 136px;
    display: flex;
    flex-direction: row;
    margin-top: 25px;
    margin-bottom: 25px;
}

#numberPlayers {
    font-size: 24px;
    width: 230px;
    padding-top: 35px;
    padding-left: 15px;
}

#number-players-button-container {
    width: 84px;
    height: 136px;
    margin-top: 13px;
}

#number-players-up {
    background-color: transparent;
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
    border: 0;
    cursor: pointer;
}

#number-players-up:focus {
    outline: none;
}

#number-players-up:active {
    margin-top: 3px;
    height: 47px;
    /* style={{ backgroundImage: "url(" + process.env.PUBLIC_URL + "/imgs/up-button-pressed.png)" }} */
}

#number-players-down {
    background-color: transparent;
    width: 50px;
    height: 50px;
    border: 0;
    cursor: pointer;
}

#number-players-down:focus {
    outline: none;
}

#number-players-down:active {
    margin-top: 3px;
    height: 47px;
    /* style={{ backgroundImage: "url(" + process.env.PUBLIC_URL + "/imgs/down-button-pressed.png)" }} */
}

#number-players-continue, #number-players-new-league {
    background-color: transparent;
    width: 314px;
    height: 50px;
    margin-bottom: 25px;
    border: 0;
    cursor: pointer;
    font-size: 24px;
}

#number-players-continue:focus, #number-players-new-league:focus {
    outline: none;
}

#number-players-continue:active, #number-players-new-league:active {
    padding-top: 4px;
    margin-top: 3px;
    height: 47px;
    /* style={{ backgroundImage: "url(" + process.env.PUBLIC_URL + "/imgs/big-button-pressed.png)" }} */
}
#players-names-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.ribbon {
    background-repeat: no-repeat;
    width: 306px; 
    height: 36px;
    padding-top: 13px;
    line-height: 20px;
    margin-top: 25px;
    margin-bottom: 15px;
}

#players-names-input-container {
    margin: auto;
    padding-left: 15px;
    max-width: 1500px;
}

.players-names-input {
    font-size: 16px;
    background-repeat: no-repeat;
    background-color: transparent;
    border: none;
    padding-left: 15px;
    width: 165px;
    height: 35px;
    margin-top: 10px;
}

.players-names-input:focus {
    outline: none;
}

#players-names-continue {
    font-size: 16px;
    background-color: transparent;
    width: 200px;
    height: 40px;
    margin-top: 32px;
    margin-bottom: 25px;
    border: 0;
    cursor: pointer;
}

#players-names-continue:focus {
    outline: none;
}

#players-names-continue:active {
    padding-top: 3px;
    margin-top: 35px;
    height: 37px;
    /* style={{ backgroundImage: "url(" + process.env.PUBLIC_URL + "/imgs/small-button-pressed.png)" }} */
}
#schedule-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.ribbon {
    background-repeat: no-repeat;
    width: 306px; 
    height: 36px;
    padding-top: 10px;
    margin-top: 25px;
    margin-bottom: 15px;
}

#schedule-matches-container {
    margin: auto;
    max-width: 1500px;
}

.schedule-match {
    background-repeat: no-repeat;
    width: 294px;
    height: 136px;
    margin: 5px;
    display: inline-block;
}

.schedule-match-played {
    cursor: pointer;
}

.schedule-player-one, .schedule-player-two {
    padding-top: 27px;
    display: flex;
    width: 100%;
}

.schedule-player-name {
    width: 76%;
    font-size: 24px;
}

.schedule-player-points {
    width: 25%;
    font-size: 20px;
    padding-top: 3px;
}

.schedule-player-two {
    padding-top: 36px;
}

#schedule-positions, #schedule-next-match {
    font-size: 16px;
    background-color: transparent;
    background-repeat: no-repeat;
    width: 200px;
    height: 40px;
    margin-left: 5px;
    margin-right: 5px;
    border: 0;
    cursor: pointer;
}

#schedule-positions {
    margin-top: 10px;
    margin-bottom: 10px;
}

#schedule-next-match {
    margin-bottom: 25px;
}

#schedule-positions:focus, #schedule-next-match:focus {
    outline: none;
}

#schedule-positions:active, #schedule-next-match:active {
    height: 37px;
    padding-top: 3px;
    /*style={{ backgroundImage: "url(" + process.env.PUBLIC_URL + "/imgs/small-button-pressed.png)" }}*/
    background-repeat: no-repeat;
}

#schedule-positions:active {
    margin-top: 13px;
}

#schedule-next-match:active {
    margin-top: 3px;
}
#positions-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.ribbon {
    background-repeat: no-repeat;
    width: 306px; 
    height: 36px;
    padding-top: 10px;
    margin-top: 25px;
    margin-bottom: 15px;
}

#positions-table {
    margin: auto;
    border-collapse: collapse;
    width: 300px;
}

.positions-table-head {
    padding: 15px 5px;
}

.positions-table-data {
    min-width: 25px;
    max-width: 25px;
    padding: 15px 5px;
}

.positions-table-data-name {
    min-width: 105px;
    text-align: left;
    padding-left: 10px;
}

#positions-schedule, #positions-next-match {
    font-size: 16px;
    background-color: transparent;
    background-repeat: no-repeat;
    width: 200px;
    height: 40px;
    margin-left: 5px;
    margin-right: 5px;
    border: 0;
    cursor: pointer;
}

#positions-schedule{
    margin-top: 10px;
    margin-bottom: 10px;
}

#positions-next-match {
    margin-bottom: 25px;
}

#positions-schedule:focus, #positions-next-match:focus {
    outline: none;
}

#positions-schedule:active, #positions-next-match:active {
    height: 37px;
    padding-top: 3px;
    /* style={{ backgroundImage: "url(" + process.env.PUBLIC_URL + "/imgs/small-button-pressed.png)" }} */
    background-repeat: no-repeat;
}

#positions-schedule:active {
    margin-top: 13px;
}

#positions-next-match:active {
    margin-top: 3px;
}
#next-match-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.ribbon {
    background-repeat: no-repeat;
    width: 306px; 
    height: 36px;
    padding-top: 10px;
    margin-top: 25px;
    margin-bottom: 15px;
}

#next-match-match {
    background-repeat: no-repeat;
    width: 294px;
    height: 136px;
    margin: auto;
    display: inline-block;
}

#next-match-player-one, #next-match-player-two {
    padding-top: 27px;
    display: flex;
    width: 100%;
}

.next-match-player-name {
    width: 76%;
    font-size: 24px;
}

#next-match-player-one-points, #next-match-player-two-points {
    width: 25%;
    font-size: 20px;
    padding-top: 3px;
    text-align: center;
    background: transparent;
    border: none;
    outline: none;
}

#next-match-player-two {
    padding-top: 36px;
}

#next-match-back, #next-match-apply {
    font-size: 16px;
    background-color: transparent;
    background-repeat: no-repeat;
    width: 200px;
    height: 40px;
    margin-left: 5px;
    margin-right: 5px;
    border: 0;
    cursor: pointer;
}

#next-match-back {
    margin-top: 10px;
    margin-bottom: 10px;
}

#next-match-apply {
    margin-bottom: 25px;
}

#next-match-back:focus, #next-match-apply:focus {
    outline: none;
}

#next-match-back:active, #next-match-apply:active {
    height: 37px;
    padding-top: 3px;
    /*style={{ backgroundImage: "url(" + process.env.PUBLIC_URL + "/imgs/small-button-pressed.png)" }}*/
    background-repeat: no-repeat;
}

#next-match-back:active {
    margin-top: 13px;
}

#next-match-apply:active {
    margin-top: 3px;
}
